import {useContext} from "react";

import { Link }  from 'react-router-dom';

//formik-102 episode
// import {useFormik} from "formik";

//formik-103episode
import{ Formik , Form , Field , ErrorMessage } from "formik";

import {contactSchema} from "../../validations/contactValidation";
///
import{ContactContext} from "../../context/contactContext";

import {Spinner} from "../";
import{Comment , Green , Purple } from "../../helpers/colors";


const AddContact =() =>
{
    const {loading,groups,createContact } = useContext(ContactContext);

   //formik
   //  const formik = useFormik({

    // });
    return(
      <>
          {loading ? (<Spinner />):
              (
                <>
                <section className="p-3">
                    <img
                        src={require("../../assets/man-taking-note.png")}
                        height="400px"
                        style={{
                            position: "absolute",
                            zIndex:"-1",
                            top:"130px",
                            left:"100px",
                            opacity:"50%"
                        }}
                    />
                    <div className="container">
                        <div className="row">
                            <div className="col">
                                <p
                                    className="h4 fw-bold text-center"
                                    style={{color:Green}}
                                >
                                     
                                </p>
                            </div>
                        </div>
                        <hr  style={{backgroundColor:Green }}/>
                        <div className="row mt-5">
                           <div className="col-md-4">
                              {/*//for showing errors : if errors are exists,run these codes.*/}
                              {/* {errors?.map((error,index)  =>(*/}
                              {/*     <p  key={index} className="text-danger" >*/}
                              {/*         {error.message}*/}
                              {/*      </p>*/}
                              {/* ))  }*/}
                              {/* */}
                               <Formik
                                   initialValues = {{
                                   fullname:"" ,
                                   photo: "",
                                   mobile: "",
                                   email: "",
                                   job: "",
                                   group:""
                               }}
                               validationSchema = {contactSchema }
                               onSubmit = { (values) => {
                               console.log(values);
                               createContact(values);
                               }}
                               >
                                   {/*{(formik) => (*/}
                                           <Form >
                                               <div className="mb-2">
                                                   <Field
                                                       name="fullname"
                                                       type="text"
                                                       className="form-control"
                                                       placeholder="   "
                                                   />

                                                   <ErrorMessage
                                                       name="fullname"
                                                       render={msg =>(
                                                           <div className="text-danger"> {msg}</div>
                                                       ) }
                                                   />
                                               </div>
                                               <div className="mb-2">
                                                   <Field
                                                       name="photo"
                                                       type="text"
                                                       className="form-control"
                                                       placeholder=" "
                                                   />
                                                   <ErrorMessage
                                                       name="photo"
                                                       render={msg => (
                                                           <div className="text-danger"> {msg}</div>
                                                       )}
                                                   />
                                               </div>
                                               <div className="mb-2">
                                                   <Field
                                                       name="mobile"
                                                       type="number"
                                                       className="form-control"
                                                       placeholder=" "
                                                   />

                                                   <ErrorMessage
                                                       name="mobile"
                                                       render={msg => (
                                                           <div className="text-danger"> {msg}</div>
                                                       ) }
                                                   />
                                               </div>
                                               <div className="mb-2">
                                                   <Field
                                                       name="email"
                                                       type="email"
                                                       className="form-control"
                                                       placeholder=" "
                                                   />

                                                   <ErrorMessage
                                                       name="email"
                                                       render={msg =>(
                                                           <div className="text-danger"> {msg}</div>
                                                       )}
                                                   />
                                               </div>
                                               <div className="mb-2">
                                                   <Field
                                                       name="job"
                                                       type="text"
                                                       className="form-control"
                                                       placeholder=""
                                                   />

                                                   <ErrorMessage
                                                       name="job"
                                                       render={msg => (
                                                           <div className="text-danger"> {msg}</div>
                                                       )}
                                                   />
                                               </div>
                                               <div className="mb-2">
                                                   <Field
                                                       name="group"
                                                       as = "select"
                                                       className="form-control"
                                                   >
                                                   <ErrorMessage
                                                       name="group"
                                                       render={msg =>(
                                                           <div className="text-danger"> {msg}</div>
                                                       )}
                                                   />

                                                       <option >   </option>

                                                       { groups.length>0 &&  groups.map((group) =>(
                                                           <option key={group.id} value={group.id}>
                                                               {group.name}
                                                           </option>
                                                       ))
                                                       }
                                                   </Field>
                                               </div>
                                               <div className="mx-2">
                                                   <input
                                                       type="submit"
                                                       className="btn"
                                                       style={{backgroundColor: Purple}}
                                                       value="  "
                                                   />
                                                   <Link
                                                       to={"/contacts"}
                                                       className="btn mx-2"
                                                       style={{backgroundColor:Comment}}
                                                   >
                                                       
                                                   </Link>
                                               </div>
                                           </Form>
                                   {/*) }*/}
                               </Formik>
                           </div>
                        </div>
                    </div>
                </section>
                </>
              )
          }
      </>
    );
}
export default AddContact;